<template>
  <div class="posts">
    <h1 class="posts__title">Posts</h1>
    <div class="posts__block">
      <div class="posts__item" v-for="post in posts" :key="post.id">
        <p class="posts__post-title">{{ post.title }}</p>
        <p class="posts__body">{{ post.body }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  computed: {
    ...mapState(["posts"]),
  },
};
</script>
